import React, {Component} from 'react';
import '../projects.css';

class Caase extends Component {
  constructor(props){
    super(props);

    this.state = {
      display: 'inherit'
    }

    this.handleScroll = this.handleScroll.bind(this);
  }

  componentDidMount(){
    window.addEventListener('scroll', this.handleScroll)
  }
  componentWillUnmount(){
    window.removeEventListener('scroll', this.handleScroll);
  }

  handleScroll(){
    let pos = document.getElementById('caase-behance').getBoundingClientRect();

    if (pos.top > 0) {
      this.setState({
        display: 'inherit'
      })
    }
    if (pos.top < 0) {
      this.setState({
        display: 'none'
      })
    }
  }

  render() {
    return(
      <div>
        <div className='white-cover'>
          <div style={{left: '0', width: '100%', height: "0", position: "relative", paddingBottom: "141.4227%"}}>
            <div style={{ display: this.state.display, zIndex:'10000', backgroundColor: 'rgba(255,255,255,0)', top: "0", left: "0", width: "100%", height: "100%", position: "absolute"}} title="CAASE-behance"  ></div>
            <iframe id="caase-behance" src="https://www.behance.net/gallery/60079065/CAASE?iframe=1" style={{border: "0", top: "0", left: "0", width: "100%", height: "100%", position: "absolute"}} title="CAASE-behance" ></iframe>
          </div>
        </div>
      </div>
    );
  }
}

export default Caase;
